<template>
  <div class="tmpl">
    <nav-bar title="注册账号"></nav-bar>
    <div class="reg">
      <mt-field placeholder="用户名" v-model="username"></mt-field>
      <mt-field placeholder="6-12位密码" type="password" v-model="password"></mt-field>
      <mt-field placeholder="手机号/邮箱" v-model="phoneOremail"></mt-field>
      <div class="cap">
        <span><mt-field placeholder="验证码" v-model="captcha" ></mt-field></span>
        <span><mt-button type="default" v-text="send" @click="getCode()"></mt-button></span>
      </div>
      <div class="reg-btn">
        <mt-button type="primary" size="large" @click="register()">注册</mt-button>
        </div>
      </div>
    </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  export default {
    name: "Register",
    data(){
        return{
          username:'',//用户名
          phoneOremail:'',//注册账号
          captcha:'',//手机验证码
          password:'',//密码
          send:"发送验证码",
          count:60,
        }
      },
    methods:{
      getCode() {
        if(this.phoneOremail.length != 0){
            this.$axios.post(this.$host+"/api/getCode/","cid=1&to="+this.phoneOremail)
              .then(res=>{
                console.log(res.data);
                if(res.data.code == 200){
                  Toast(res.data.msg);
                  let i = setInterval(() => {
                    if (this.count == 0) {
                      this.count = 60;
                      this.send = "发送验证码";
                      clearInterval(i);
                    }else{
                      this.send = (this.count--)+"秒重发";
                    }
                  }, 1000);
                }else{
                  Toast(res.data.msg);
                  return;
                }
              })
          }
      },
      register(){
        if(this.username.length == 0){
          Toast("请正确填写用户名");
          return;
        }
        if(this.phoneOremail.length == 0){
          Toast("请正确填写手机号或邮箱");
          return;
        }
        if(this.password.length>12 || this.password.length<6){
          Toast("请正确填写密码");
          return;
        }
        if (this.captcha.length != 6){
          Toast("请正确填写验证码");
          return;
        }
        this.$axios.post(this.$host+"/api/register/","username="+this.username+"&user="+this.phoneOremail+"&password="+this.password+"$code="+this.captcha)
          .then(res=>{
            if(res.data.code == 200){
              Toast(res.data.msg);
            }else{
              Toast(res.data.msg);
            }
          })
          .catch(err=>{
            console.log(err);
          })
      }
    }
  }
</script>

<style scoped>
  .reg{
    padding-top: 20px;
  }
  .cap > span{
    width: 50%;
    line-height: 25px;
    float: left;
  }
  .reg-btn{
    padding: 0 10px;
    margin-top: 25px;
  }
</style>



